.shopCategoryItem {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 20px;
}
.shopCategoryItem2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 20px;
}
.shopCategoryItem .itemComponent {
  width: 14vw;
}
@media only screen and (max-width: 600px) {
  .shopCategoryItem {
    flex-direction: column;
    align-items: center;
  }
  .shopCategoryItem2 {
    flex-direction: column;
    align-items: center;
  }
  .shopCategoryItem .itemComponent {
    width: 85vw;
  }
  .shopCategoryItem2 .itemComponent {
    width: 85vw;
  }
}

