.checkout-page {
  width: 90vw;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;
}
.checkout-header {
  width: 100%;
  padding: 10px 0;
}
.checkout-header .checkoutItem {
  border-bottom: 1px solid darkgrey;
}
.header-block {
  text-transform: capitalize;
}
